.Joke{
    display:flex;
    border-bottom:2px solid;
    align-items: center;
    justify-content: center;
}
.Joke-buttons{
    display: flex;
}
.Joke-text{
    width:75%;
    font-size:1.2rem;
}
.Joke-votes{
    width:50px;
    height:50px;
    line-height:50px;
    border-radius:50%;
    text-align:center;
    font-size:20px;
    font-weight:300;
    border:3px solid red;
    box-shadow:0 10px 38px rgba(0,0,0,0.2),0 10px 12px rgba(0,0,0,0.2);
}
.fa-arrow-up,
.fa-arrow-down{
    font-size:1.5rem;
    margin:10px;
    cursor:pointer
}
.Joke-smiley{
    font-size: 3rem;
    margin-left: auto;
    border-radius: 50%;
}